<script>
import * as d3 from "d3";
import { h } from 'vue';
import { hierarchy, tree } from 'd3';
export default {
    name: "SpecInfo",
    data() {
        return {
            chartData: {
                name: "",
                children: [
                    {
                        name: "",
                        children: [
                            { name: "", value: 20 },
                            { name: "", value: 20 },
                        ]
                    },
                    {
                        name: "",
                        children: [
                            { name: "", value: 50 },
                            // { name: "", value: 15 },
                        ]
                    },
                    {
                        name: "",
                        children: [
                            { name: "", value: 60 },
                        ]
                    }
                ]
            },
            IcicleData : {
                //  level 
                name: '',
                level: 0, // 
                group: 0,               
                children: [
                    {
                        name: '',
                        level: 1, // 
                        group: 1,
                        children: [
                            {
                                name: '',
                                level: 2, // 
                                group: 1.1,
                                children: [
                                    { name: '', value: 3, level: 3 },
                                    
                                ]
                            },
                            {
                                name: '',
                                level: 2, // 
                                group: 1.1,
                                children: [
                                    { name: '', value: 3, level: 3 },
                                   
                                ]
                            }
                        ]
                    },
                    {
                        name: '',
                        level: 1, // 
                        group: 2,
                        children: [
                            {
                                name: '',
                                level: 2, // 
                                group: 2.1,
                                children: [
                                    { name: '', value: 3, level: 3 },
                                    // { name: '2', value: 1 }
                                ]
                            }                            
                        ]
                    },
                    {
                        name: '',
                        level: 1, // 
                        group: 3,
                        children: [
                                {
                                    name: '',
                                    level: 2, // 
                                    group: 3.1,
                                    children: [
                                        { name: '', value: 3, level: 3 },
                                        // { name: '2', value: 1 }
                                    ]
                                }
                            ]
                    },
                ]
            }
        };
    },
    mounted(){
        this.drawTree();
        this.drawIciclePlot();
        this.generateComponentMap();
        this.generateInterMap();
        this.drawGroup();
    },
    methods:{
        generateComponentMap(){
            var svg = d3.select("#component")
                .append("g")
                .attr("transform", "translate(130,125)");
            // 
            var circle = svg.append("circle")
            circle.attr("cx",0)
                .attr("cy",0)
                .attr("r",60)
                .style("cursor", "pointer") // 
                .attr("fill","antiquewhite")
            //Adding text labels to the circle
            var text1 = svg.append("text");
            //Styling text
            text1.attr("font-family",  "Courier")
                .attr("fill", "black")
                .attr("dx",2)
                .attr("dy",2)
                .text("")
                // .style("opacity", "0.8")
                .attr("font-size", "1em")
                .attr("font-weight", "700")
                .attr("text-anchor",  "middle");
            
            // 
            circle.on("mouseover", function() {
                d3.select(this)
                    .attr("fill", "rgb(250,250,120)")  // 
                    .attr("stroke", "black")  // 
                    .attr("stroke-width", 2);  // 
            });

            // 
            circle.on("mouseout", function() {
                d3.select(this)
                    .attr("fill", "antiquewhite")  // 
                    .attr("stroke", "none");  // 
            });
  
            // Function is used 
            var arc = d3.arc()
                .innerRadius(60)
                .outerRadius(120)
                .startAngle(100)
                .endAngle(2 * 180);
    
            svg.append("path")
                .attr("class", "arc")
                .attr("d", arc)
                .attr("fill"," skyblue");
             //Adding text labels to the circle
             var text2 = svg.append("text");
            //Styling text
            text2.attr("font-family",  "Courier")
                .attr("fill", "black")
                .attr("dx",0)
                .attr("dy",90)
                // .text("")
                .text("-------")
                .attr("font-size", "2em")
                .attr("font-weight", "700")
            
        },
        generateInterMap(){
            let drag = simulation => {
            function dragstarted(event) {
                if (!event.active) simulation.alphaTarget(0.3).restart()
                event.subject.fx = event.subject.x
                event.subject.fy = event.subject.y
            }
  
            function dragged(event) {
                event.subject.fx = event.x
                event.subject.fy = event.y
            }
  
            function dragended(event) {
                if (!event.active) simulation.alphaTarget(0)
                event.subject.fx = null;
                event.subject.fy = null;
            }
  
            return d3.drag()
                .on("start", dragstarted)
                .on("drag", dragged)
                .on("end", dragended)
            }

            const scale = d3.scaleOrdinal(d3.schemeCategory10)
            const color = d => scale(d.group)

            let height = 126
            let width = 160

            let data = {
            "nodes": [
                {"id": "", "group": 1, "size": 12},
                {"id": "", "group": 2, "size": 8},
                {"id": "", "group": 2, "size": 8},
                {"id": "", "group": 2, "size": 8},
                {"id": "", "group": 3, "size": 5},
                {"id": "", "group": 3, "size": 5},               
                {"id": "", "group": 3, "size": 5},                
                {"id": "", "group": 3, "size": 5}

                // {"id": "", "group": 1, "size": 12},
                // {"id": "", "group": 2, "size": 8},
                // {"id": "", "group": 2, "size": 8},               
                // {"id": "", "group": 2, "size": 8},                
                // {"id": "", "group": 2, "size": 8},
                // {"id": "", "group": 3, "size": 5},
                // {"id": "", "group": 3, "size": 5},
                // {"id": "", "group": 3, "size": 5},
                
            ],
            "links": [
                {"source": "", "target": "", "value": 1},
                {"source": "", "target": "", "value": 1},
                {"source": "", "target": "", "value": 1},
                {"source": "", "target": "", "value": 1},
                {"source": "", "target": "", "value": 1},
                {"source": "", "target": "", "value": 1},
                {"source": "", "target": "", "value": 1}

                // {"source": "", "target": "", "value": 1},
                // {"source": "", "target": "", "value": 1},
                // {"source": "", "target": "", "value": 1},
                // {"source": "", "target": "", "value": 1},
                // {"source": "", "target": "", "value": 1},
                // {"source": "", "target": "", "value": 1},
                // {"source": "", "target": "", "value": 1},
                // {"source": "", "target": "", "value": 1}
                ]
            }

            const links = data.links.map(d => Object.create(d))
            const nodes = data.nodes.map(d => Object.create(d))

            const simulation = d3.forceSimulation(nodes)
            .force("link", d3.forceLink(links).id(d => d.id))
            .force("charge", d3.forceManyBody())
            .force("center", d3.forceCenter(width / 2, height / 2))

            const svg = d3.select("#interact")
            .attr("viewBox", [10, -10, width * 1.2, height * 1.2])

            const link = svg.append("g")
                .attr("stroke", "#999")
                .attr("stroke-opacity", 0.6)
                .selectAll("line")
                .data(links)
                .join("line")
                .attr("stroke-width", d => Math.sqrt(d.value))

            const node = svg.append("g")
                .attr("stroke", "#fff")
                .attr("stroke-width", 1.5)
                .selectAll("circle")
                .data(nodes)
                .join("circle")
                .attr("r", d => d.size)
                .attr("fill", color)
                .call(drag(simulation))
            
             //
            const text = svg.append("g")
                    .selectAll("text")
                    .data(nodes)
                    .join("text")
                    .attr("fill","black")
                    .attr("dx",d => d.size)
                    .attr("dy",d => d.size)
                    .text(d => d.id)
                    .attr("font-size",d => d.size)

            node.append("title")
                .text(d => d.id)

            simulation.on("tick", () => {
            link
                .attr("x1", d => d.source.x)
                .attr("y1", d => d.source.y)
                .attr("x2", d => d.target.x)
                .attr("y2", d => d.target.y)

            node
                .attr("cx", d => d.x)
                .attr("cy", d => d.y)
            
            text
                .attr("x", d => d.x)
                .attr("y", d => d.y)
            })   
        },
        drawIciclePlot() {
            const width = 1750;
            const height = 450;

            const svg = d3
                .select('#icicle-plot-interaction')
                .append('svg')
                .attr('width', width)
                .attr('height', height);

            const color = d3.scaleOrdinal()
                            .range(['#2b8cbe', '#4eb3d3', '#7bccc4', '#a8ddb5', '#ccebc5', '#e0f3db', '#f7fcf0'])
                            // .range(["#4eb3d3","#7bccc4","#a8ddb5","#ccebc5","#e0f3db","#f7fcf0"])

            const partition = d3.partition().size([width, height]);
            
            


            const root = d3.hierarchy(this.IcicleData)
                .sum((d) => d.value)
                .sort((a, b) => b.value - a.value);

            partition(root);

            const cell = svg
                .selectAll('g')
                .data(root.descendants())
                .join('g')
                .attr('transform', (d) => `translate(${d.x0},${d.y0})`)
                .attr('class', 'rectangle') //rectangle

            cell
                .append('rect')
                .attr('width', (d) => d.x1 - d.x0)
                .attr('height', (d) => d.y1 - d.y0)
                //  group  SVG  fill 
                .attr('fill', (d) => color((d.children ? d : d.parent).data.group))
                // .attr('fill', (d) => color(d.data.level))
                

            
                // foreignObjectdivCSSforeignObject
                // display: flexjustify-content: centeralign-items: center
                cell
                .append('foreignObject')
                .attr('x', 4) // 
                .attr('y', 4) // 
                .attr('width', (d) => d.x1 - d.x0 - 8) // 
                .attr('height', (d) => d.y1 - d.y0 - 8) // 
                .append('xhtml:div')
                // .style('width', (d) => d.x1 - d.x0 - 8 + 'px') // 
                // .style('height', (d) => d.y1 - d.y0 - 8 + 'px') // 
                // .style('overflow', 'auto') // 
                .style('width', '100%')
                .style('height', '100%')
                .style('display', 'flex')
                .style('justify-content', 'center')
                .style('align-items', 'center')
                // .style('color', 'white')
                .style('font-weight',700)
                // .style('title',(d) => d.data.name)
                .html((d) => d.data.name)
                .on('mouseover', function () {
                    d3.select(this)
                        .style('background-color', 'rgba(255, 255, 120)'); // 
                })
                .on('mouseout', function () {
                    d3.select(this)
                        .style('background-color', null); // 
                });

                // ...

        },
        drawTree() {
            const data = {
                name: '',
                children: [
                    {
                        name: '',
                        children: [
                            { 
                                name: '',                                
                            },
                            { 
                                name: '',
                                children: [
                                    { 
                                        name: '',
                                        children: [
                                            { name: '' },
                                            { name: '' },
                                        ], 
                                    },
                                    // { 
                                    //     name: '',
                                    //     children: [
                                    //         { name: '' },
                                           
                                    //     ], 
                                    // },
                                    // { 
                                    //     name: '',
                                    //     children: [
                                    //         { name: '' },
                                            
                                    //     ], 
                                    // },
                                ], 
                            
                            },
                        ],
                    },
                    {
                        name: '',
                        children: [
                            { 
                                name: '',
                            },
                            { 
                                name: '',
                                children: [                                    
                                    { 
                                        name: '',
                                        children: [
                                            { name: '' },                                          
                                        ], 
                                    },                                    
                                ], 
                            
                            },
                        ],
                    },
                    {
                        name: '',
                        children: [
                                    { 
                                        name: '',
                                        children: [
                                            { name: '' },                                          
                                        ], 
                                    },  
                                ], 
                        // children: [
                        //     { 
                        //         name: '',
                                
                        //     },
                        //     { 
                        //         name: '',
                        //         children: [
                        //             { 
                        //                 name: '',
                        //                 children: [
                        //                     { name: '' },                                          
                        //                 ], 
                        //             },  
                        //         ], 
                            
                        //     },
                        // ],
                    },
                ],
            };

            const width = 1780;
            const height = 1000;
            const scale = d3.scaleOrdinal(d3.schemeCategory10)
            // const color = d => scale(d.group)
            
            const svg = d3.select(this.$refs.treeContainer)
                        .attr("width", width)
                        .attr("height", height)
                        .append("g")
                        .attr("transform", "translate(100,0)");
            
            // const root = hierarchy(data);
            // const treeLayout = tree().size([1000, 1200]); // 

            const root = d3.hierarchy(data);
            // const root = d3.hierarchy(this.IcicleData);
            const treeLayout = d3.tree().size([1000, 1200]); // 

            // 
            // const treeLayout = d3.tree().size([height, width]);

            treeLayout(root);

            // 
            svg.append("defs")
                .append("marker")
                .attr("id", "arrowhead")
                .attr("viewBox", "0 -5 10 10")
                .attr("refX", 18)  // Adjust this value to control the position of the arrowhead along the link
                .attr("refY", 0)
                .attr("markerWidth", 16)
                .attr("markerHeight", 16)
                .attr("orient", "auto")
                .append("path")
                .attr("d", "M0,-5L10,0L0,5") // 
                .attr("fill", "rgb(255, 0, 0, 0.6)"); // 
            
            const links = svg.selectAll('.link')
                .data(root.links())
                .join('path')
                .attr('class', 'link')
                .attr('d', (d) => {
                    // return `M${d.source.y},${d.source.x} ${d.target.y},${d.target.x}`;
                    // return `M${d.source.y},${d.source.x}C${(d.source.y + d.target.y) / 2},${d.source.x} ${(d.source.y + d.target.y) / 2},${d.target.x} ${d.target.y},${d.target.x}`;
                    return `M${d.target.y},${d.target.x}C${(d.source.y + d.target.y) / 2},${d.target.x} ${(d.source.y + d.target.y) / 2},${d.source.x} ${d.source.y},${d.source.x}`;

                });

            links.filter((d) => d.target.data.name === '' || d.target.data.name === '' 
                                || d.target.data.name === '' || d.target.data.name === '')
                .attr('marker-end', 'url(#arrowhead)'); 

            const nodeSize = 40; // 

            const nodes = svg
                .selectAll(".node")
                .data(root.descendants())
                .join("g")
                .attr("class", "node")
                .attr("transform", (d) => `translate(${d.y},${d.x})`);

            nodes.append("circle").attr("r", 20);

            //   rectangle
            const rectWidth = 50;
            const rectHeight = 50;

            nodes
                .filter((d) => d.depth === 1 && d.data.name === "") // 
                .append("rect")
                .attr("x", -rectWidth / 2)
                .attr("y", -rectHeight / 2)
                .attr("width", rectWidth)
                .attr("height", rectHeight)
                .attr("rx", 5) // 
                .attr("ry", 5)
                .style("fill","#87CEFA;")
                // .style("fill", "lightblue");

            //   diamond
            const diamondSize = 30;

            const diamondPoints = [
                    [0, -diamondSize],
                    [diamondSize, 0],
                    [0, diamondSize],
                    [-diamondSize, 0],
                ];

            const lineFunction = d3.line().x((d) => d[0]).y((d) => d[1]).curve(d3.curveLinearClosed);

            nodes
                .filter((d) => d.depth === 1 && d.data.name === "") // 
                .append("path")
                .attr("d", lineFunction(diamondPoints))
                .style("fill","#87CEFA;")
                // .style("fill", "lightgreen");

            //   triangle
            const triangleSize = 30;
            const triangleHeight = (Math.sqrt(3) / 2) * triangleSize;

            const trianglePoints = [
                    [0, -triangleSize],
                    [triangleHeight, triangleSize / 2],
                    [-triangleHeight, triangleSize / 2],
                ];

            nodes
                .filter((d) => d.depth === 1 && d.data.name === "") // 
                .append("path")
                .attr("d", lineFunction(trianglePoints))
                .style("fill","#87CEFA;")
                // .style("fill", "lightcoral");
            
            // 
            nodes
                .filter((d) => d.depth === 1 && (d.data.name === "" || d.data.name === "" || d.data.name === ""))
                .select("circle")
                .remove();
            
            //  foreignObject  HTML  SVG  text 
            // 
            nodes.append('foreignObject')
                .attr('x', -60)
                .attr('y', -60)
                .attr('width', 400) // 
                .attr('height', 60) // 
                .html((d) => {
                    if (d.data.name.includes('') || d.data.name.includes('')) {
                        return `<div class="tree_item">
                                    <span class="up_arrow"></span>
                                    <span class="tree_text">${d.data.name}</span>
                                </div>`;
                    } else if (d.data.name.includes('') || d.data.name.includes('')) {
                        return `<div class="tree_item">
                                    <span class="down_arrow"></span>
                                    <span class="tree_text">${d.data.name}</span>
                                </div>`;
                    } else {
                        return d.data.name;
                        }
                })
                .on('mouseover', function() {
                    d3.select(this)
                        .style('font-size', '28px'); // 
                    d3.select(this.parentNode)
                        .select('circle')
                        .attr('transform', 'scale(1.5)'); // 
                })
                .on('mouseout', function() {
                    d3.select(this)
                        .style('font-size', null); // 
                    d3.select(this.parentNode)
                        .select('circle')
                        .attr('transform', 'scale(1)'); // 
                });


            // text
            // nodes.append('text')
            //     .attr('x', -60)
            //     .attr('y', -30)
            //     .style("fill","black")
            //     // .style("fill", (d) => {
            //     //     if (d.data.name.includes(''|| '')) {
            //     //         return "red"; // 
            //     //     } else {
            //     //         return "black";
            //     //     }
            //     // })
        
                // .text((d) => {
                //     if (d.data.name.includes(''|| '')) {
                //         return '\u2191 ' + d.data.name; // 
                //     } else {
                //         return d.data.name;
                //     }
                // });
                // .text((d) => d.data.name);
            
            nodes.attr('class', (d) => {
                //  CSS 
                if (d.data.name.includes('')) {
                    return 'node-positive'; //  CSS  node-positive
                } else if (d.data.name.includes('')) {
                    return 'node-negative'; //  CSS  node-negative
                } else {
                    return 'node-default'; //  CSS  node-default
                }
            });
            
            // nodes
            //     .on('mouseover', function() {
            //         d3.select(this).select('circle')
            //             .attr('transform', 'scale(1.5)'); // 
            //         d3.select(this).select('text')
            //             .style('font-size', '30px'); // textforeignObject
            //     })
            //     .on('mouseout', function() {
            //         d3.select(this).select('circle')
            //             .attr('transform', 'scale(1)'); // 
            //         d3.select(this).select('text')
            //             .style('font-size', null); // 
            //     });

        },
        drawGroup(){
            const width = 800;
            const height = 500;
            
            const svg = d3.select(this.$refs.svgContainer)
                .attr("width", width)
                .attr("height", height)
                .append("g")
                .attr("transform", "translate(-10,0)");
            
            // 
            const pack = d3.pack()
                .size([width, height])
                .padding(3);

            // 
            // const colorScale = d3.scaleOrdinal(d3.schemeCategory10);
            const colorScale = d3.scaleOrdinal()
                        .range(['#7bccc4', '#a8ddb5', '#ccebc5', '#e0f3db', '#f7fcf0'])
            
            // 
            const root = d3.hierarchy(this.chartData)
                .sum(d => d.value || 0);
            
            // 
            const nodes = pack(root).descendants();        
        
            // 
            const circles = svg.selectAll("circle")
                .data(nodes)
                .enter()
                .append("circle")
                .attr("cx", d => d.x)
                .attr("cy", d => d.y)
                .attr("r", d => d.r)
                .style("cursor", "pointer") // 
                .attr("fill", d => colorScale(d.depth))
                
            const baseFontSize = 30;
            // 
            const labels = svg.selectAll("foreignObject")  // foreignObjecthover
                .data(nodes)
                .enter()
                .append("foreignObject")
                .attr("x", d => d.x - d.r)
                .attr("y", d => d.y - d.r)
                .attr("width", d => d.r * 2) // 
                .attr("height", d => d.r * 2) // 
                .append("xhtml:div")
                // foreignObjectcss
                .attr("class", "circle-text") // CSS
                // .style("width", d => (d.r * 2) + "px")
                // .style("height", d => (d.r * 2) + "px")
                .style("text-align", "center")
                .style("font-weight", 700)
                .style("font-size", d => {
                    // 
                    return baseFontSize / (d.depth * 0.5 + 1) + "px";
                })
                .style("cursor", "pointer")
                .style("word-wrap", "break-word") // 
                .text(d => d.data.name)
                .on("mouseover", function () {
                    d3.select(this)
                        .style("border","3px solid orange")
                        .style("font-size",  d => {
                            // 
                            // const baseFontSize = 32;
                            return (baseFontSize / (d.depth * 0.5 + 1))*1.2 + "px";
                    })
                })
                .on("mouseout", function () {
                    d3.select(this)
                        .style("border",null)
                        .style("font-size", d => {
                            // 
                            // const baseFontSize = 32;
                            return baseFontSize / (d.depth * 0.5 + 1) + "px";
                    })
                })
                .each(function (d) {
                    // 
                    const div = d3.select(this);
                    const circleRadius = d.r;
                    if (d.depth === 0 || d.depth === 1) {
                        const x = d.x;
                        const y = d.y;
                        div.style("padding-top", "16px");                     
                    }
                    else if (d.depth === 2) {
                        const x = d.x;
                        const y = d.y;
                        div.style("line-height", "1.2"); // 
                        div.style("align-items","center") // 
                        // div.style("margin-top", "50%"); // 
                        // div.style("transform", "translateY(-50%)"); // 
                    }
            });
        }
    }
};

</script>

