<script>
import * as d3 from 'd3';
import { select } from 'd3-selection';
import { hierarchy, tree } from 'd3-hierarchy';
// import Storage from './Storage.vue'
export default {
  name: "Attention",
  components: { 
    
  },
  mounted() {
    this.drawIciclePlot();
  },
  methods: {
    drawIciclePlot() {
      const width = 1750;
      const height = 450;

      const svg = d3
        .select('#icicle-plot-container')
        .append('svg')
        .attr('width', width)
        .attr('height', height);

      const color = d3.scaleOrdinal()
                    // .range(['#2b8cbe', '#4eb3d3', '#7bccc4', '#a8ddb5', '#ccebc5', '#e0f3db', '#f7fcf0'])
                    .range(["#4eb3d3","#7bccc4","#a8ddb5","#ccebc5","#e0f3db","#f7fcf0"])


      const partition = d3.partition().size([width, height]);
      
      const data = {
          name: '',
          children: [
            {
              name: '',
              // value: 10,
              children: [
                { name: '3', value: 2 },
                { name: '5', value: 2 },
                { name: '', value: 2 },
                { name: '', value: 2 }
              ]
            },
            {
              name: '',
              // value: 8,
              children: [
                { name: '', value: 2 },
                { name: '', value: 2 },
                { name: '', value: 2}
              ]
            },
            {
              name: '',
              // value: 10,
              children: [
                { name: '', value: 2 },
                
              ]
            },
            {
              name: '',
              // value: 8,
              children: [
                { name: '', value: 2 },
                { name: '', value: 2 },
                { name: '', value: 2},
                { name: '', value: 2 },
                { name: '', value: 2 },
                { name: '', value: 2},
                { name: '', value: 2 },
                { name: '', value: 2 },
              ]
            }
          ]
        };

      const root = d3.hierarchy(data)
        .sum((d) => d.value)
        .sort((a, b) => b.value - a.value);

      partition(root);

      const cell = svg
        .selectAll('g')
        .data(root.descendants())
        .join('g')
        .attr('transform', (d) => `translate(${d.x0},${d.y0})`)
        .attr('class', 'rectangle') //rectangle

      cell
        .append('rect')
        .attr('width', (d) => d.x1 - d.x0)
        .attr('height', (d) => d.y1 - d.y0)
        .attr('fill', (d) => color((d.children ? d : d.parent).data.name))
      
        // foreignObjectdivCSSforeignObject
        // display: flexjustify-content: centeralign-items: center
        cell
        .append('foreignObject')
        .attr('x', 4) // 
        .attr('y', 4) // 
        .attr('width', (d) => d.x1 - d.x0 - 8) // 
        .attr('height', (d) => d.y1 - d.y0 - 8) // 
        .append('xhtml:div')
        // .style('width', (d) => d.x1 - d.x0 - 8 + 'px') // 
        // .style('height', (d) => d.y1 - d.y0 - 8 + 'px') // 
        // .style('overflow', 'auto') // 
        .style('width', '100%')
        .style('height', '100%')
        .style('display', 'flex')
        .style('justify-content', 'center')
        .style('align-items', 'center')
        // .style('color', 'white')
        .style('font-weight',700)
        // .style('title',(d) => d.data.name)
        .html((d) => d.data.name)
        .on('mouseover', function () {
            d3.select(this)
                .style('background-color', 'rgba(255, 255, 120)'); // 
        })
        .on('mouseout', function () {
            d3.select(this)
                .style('background-color', null); // 
        });

        // ...

    },
  }
};

</script>

