// 
import Vue from 'vue'
import VueRouter from "vue-router";
//
Vue.use(VueRouter)
// 
import AppMain from '../pages/AppMain'
// 
import PatientMain from '../pages/PatientMain'
import ProfesMain from '../pages/ProfesMain'

// 
export default new VueRouter({
    mode: 'history', //#
    routes:[
        {
            path:'/AppMain',
            name: 'AppMain',  // 
            component:AppMain, // 
            children:[
                {
                    // path:'DrugShape', ///
                    path:'/PatientMain',
                    component:PatientMain
        
                },
                {
                    path:'/ProfesMain',
                    component:ProfesMain
        
                }                
            ]
        },  
             
    ]
})